import React, { Component } from 'react';

import "./Projects.sass";

export class Projects extends Component {
  
    projects = [
        {
            title:'🛰 freemium internet',
            description:`The true potential of the blockchain technology is far from beeing explored. 
                        This is a concept project to lay down the groundwork for a fully decentralized highly performant and secure internet model.`,
            links:[
                {label:'View the website', href:'https://igorp1.github.io/shark-site/#/'},
            ]
        },
        {
            title:'🧠 Eureka',
            description:`An iOS app built for you to keep all your great ideas. More than that, 
            you can also find potential collaborators, explore other ideas and learn from the community.
            More coming soon...`,
            links:[
                {comingSoon:true}
            ]
        },
        {
            title:'🖍 <hang>',
            description:`Fun hangman game built with Angular and Flask. Built as a special request from 3D HUBS.`,
            links:[
                {label:'View on GitHub', href:'https://github.com/igorp1/hang'},
                {label:'Live demo', href:'https://hang-webapp.herokuapp.com'},
            ]
        },
        {
            title:'🖥 idp-cli',
            description:`Handy cli tool to quickly spin up projects. It currently supports javascript, Vue, React and Angular seeds.`,
            links:[
                {label:'View on GitHub', href:'https://github.com/igorp1/idp-cli'},
                {label:'View on npm', href:'https://www.npmjs.com/package/idp-cli'},
            ]
        },
        {
            title:'🌈 cereal!',
            description:`What's bettter on a Saturday morning than cartoons and cereal? 
                        The goal for this app is to bring this feeling to you wherever you are at any time.
                        More than just a cereal delivery app, this is a robbust skeleton for an online shop featuring inventory management and direct card payments.`,
            links:[
                {label:'View on GitHub', href:'https://github.com/igorp1/cereal'},
            ]
        },
        {
            title:'🚉 igor.commute',
            description:`Simple angularJS app to make my commute easier when I lived in Boston. 
                        It uses the MBTA api to list upcoming train times for the stops relevant to me.`,
            links:[
                {label:'View on GitHub', href:'https://github.com/igorp1/igor.commute'},
                {label:'Live demo', href:''},
            ]
        },
        {
            title:'💡 Light Innovation Technology',
            description:`This was my senior design project where I had a lot of space to develop a cool web app 
                        and dip my toes into the internet of things. This is the control pipeline for a smart light bulb.
                        The smart device's python firmware executes controls fetched from our server initiated by the user on a web application.`,
            links:[
                {label:'View on GitHub', href:'https://github.com/igorp1/Light-Innovation-Technology'}
            ]
        },
        {
            title:'🔬 Particle Tracking Estimation',
            description:`In the summer of 2014 I had the opportunity to work at a research lab at Boston University. 
                        My job as a high performance computation research assistant was to lay the foundation 
                        for the research team to be able to perform parallel computations on BU’s GPU cluster using CUDA 
                        as well as implement their old algorithm into the new framework.`,
            links:[
                {label:'Download Source', href:'https://doc-0s-2k-docs.googleusercontent.com/docs/securesc/ha0ro937gcuc7l7deffksulhg5h7mbp1/r20e86q1ks49u944pje1epq8iiui7sfk/1519243200000/01139310068959731390/*/0B1lLPxEWiJiOaGctWWNJRkJ1N1k?e=download'}
            ]
        },
        {
            title:'📊 The MATLAB collection',
            description:`MATLAB was my first programming language so this collection features some of my earliest work! 
                        The highlights are the Yakhot simluator - which allows you to build a Truss design and stress test it - 
                        and Canvas - a much more reliable GUI builder than what MATLAB had to offer at the time.`,
            links:[
                {label:'View on GitHub', href:'https://github.com/igorp1/MATLAB-projects'}
            ]
        }

        
    ]

    render() {
        return (
        <div className="main-container projects">
            <h1>/projects</h1>
            <div className="breath">
                { this.renderProjects() }
            </div>
        </div>
        );
    }

    renderProjects(){
        return this.projects.map(
            (x, key) => this.renderProjectCard(x, key)
        )
    }

    renderProjectCard(project, key){
        return (
            <div key={key} className="breath" >
                    <h3>{project.title}</h3>
                    {project.description}<br /><br />
                    {this.renderLinks(project.links)}
            </div>
        );
    }

    renderLinks(linkArr){
        return linkArr.map(
            (x,key)=>{ return( x.comingSoon ? <span>More coming Soon...</span> : <a key={key} href={x.href} target="_blank">{x.label}</a> ); }
        );
    }
}



