import * as React from "react";
import { Link } from 'react-router-dom';

import './Navigation.sass';

export class Navigation extends React.Component {

    constructor(props){
        super(props)
        this.state = { showMenuItems : false }
        this.toggleButtons = this.toggleButtons.bind(this)
    }

    menuIcon = { open: "👆🏾", close:"👇🏾"};
    tabs = [
        {link:'/',          label:'👨🏾‍💻'},
        {link:'/about',     label:'💬'},
        {link:'/projects',  label:'🛰'},
        // {link:'/read',      label:'🧠'},
    ];

    renderLinks(){
        return this.tabs.map(
            (item, key) => {
                return <NavLink key={key} link={item.link} label={item.label} /> 
            }
        )
    }

    renderNavToggle(){
        return <button onClick={ this.toggleButtons }>{
            !this.state.showMenuItems ? this.menuIcon.open : this.menuIcon.close
        }</button>
    }

    toggleButtons(){
        const isShowing = this.state.showMenuItems;
        this.setState( {showMenuItems : !isShowing} )
    }


    render(){
        return (
            <div id="nav-container">
                { this.renderNavToggle() }
                { this.state.showMenuItems && this.renderLinks() }
            </div>
        );
    }
}


class NavLink extends React.Component {

    render(){
        return (
            <Link to={this.props.link}>
                <button>
                    <span role="img" aria-label={`Link to ${this.props.link}`}>{this.props.label}</span>
                </button>
            </Link>
        );
    }
}


